@use 'src/styles/mixins';

.search-section {
  width: 100%;
  min-width: 320px;
  padding: 10px 25px;
  margin-top: 20px;
  @include mixins.flexCenter(row);
  font-size: 20px;
  font-weight: 700;
  text-transform: capitalize;

  .search-form {
    width: 70%;
    min-width: 300px;
    max-width: 500px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: 20px 10px;
    background-color: lightgray;
    backdrop-filter: blur(20px);

    .search-form-control {
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;

      input {
        display: block;
        height: 50px;
        width: 100%;
        border-radius: 3px;
        padding: 0 10px;
        margin-top: 8px;
        font-size: 14px;
        font-weight: 300;
        letter-spacing: 0.5px;
        outline: none;
      }
    }
  }
}
