video {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: -1;
}

.hero-container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  object-fit: contain;
  position: relative;
  /* cover the black line of the video */
  top: -15px;
}

.hero-container > h1 {
  color: var(--mainFontColor);
  font-size: 100px;
  margin-top: -100px;
}

.hero-container > p {
  margin-top: 8px;
  color: var(--mainFontColor);
  font-size: 32px;
}
.hero-btns {
  margin-top: 32px;
  backdrop-filter: blur(10px);
}

.hero-btns .btn {
  margin: 6px;
}

.toggle-hero-video {
  position: absolute;
  top: 20px;
  left: 5px;
}

.fa-play-circle {
  margin-left: 4px;
}

@media screen and (max-width: 960px) {
  .hero-container > h1 {
    font-size: 70px;
    margin-top: -150px;
  }
}

@media screen and (max-width: 768px) {
  .hero-container > h1 {
    font-size: 50px;
    margin-top: -100px;
  }

  .hero-container > p {
    font-size: 30px;
  }

  .hero-container .btn-mobile {
    display: block;
    text-decoration: none;
  }

  .hero-container .btns {
    width: 100%;
    margin-bottom: 5px;
  }
}
